* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-family: 'Open Sans', sans-serif;
    font-size: 15px;
}

form {
    display: flex;
    flex-flow: column;
}

button {
    padding: 5px 15px;
    border-radius: 5px;
    background: white;
    font-family: inherit;
    border: 1px solid black;
    cursor: pointer;
    font-size: 15px;
    transition: transform 0.2s ease-in;
}

button:hover {
    transform: scale(1.05);
}

button:last-child {
    margin-top: 20px;
}

label {
    display: flex;
    flex-flow: row;
    align-items: center;
    margin: 10px 0;
}

span {
    flex: 0 0 50px;
    margin-right: 10px;
}

input:not([type="color"]) {
    padding: 5px 15px;
    font-family: inherit;
}

h1 {
    font-size: 40px;
    margin-bottom: 25px;
}

h5 {
    font-size: 25px;
    margin-bottom: 25px;
}

h6 {
    font-size: 20px;
    margin-bottom: 25px;
}

img {
    width: 100%;
    height: auto;
}

.app {
    padding: 20px;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}

.app-buttons {
    display: flex;
    flex-flow: row;
    justify-content: center;
    align-items: center;
}

.app-buttons button {
    margin-top: 0;
}

.app-buttons button:not(:last-child) {
    margin-right: 10px;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.3);
    height: 100%;
    width: 100%;
    animation: blackBackground 0.2s ease-in forwards;
}

.modal-content {
    border-radius: 5px;
    display: flex;
    flex-flow: column;
    padding: 20px;
    background: white;
    width: 400px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    animation: scaleIn 0.2s;
}

.canvas {
    width: 600px;
    height: 500px;
    border: 1px dashed black;
    margin-top: 20px;
}

.controls {
    position: fixed;
    background: white;
    z-index: 1;
    top: 0;
    right: 0;
    width: 300px;
    height: 100%;
    padding: 20px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    animation: slideIn 0.2s ease-in forwards;
}

.controls-form {
    display: flex;
    flex-flow: column;
}

.controls__hide-icon {
    width: 15px;
    cursor: pointer;
    position: absolute;
    top: 20px;
    right: 20px;
    transition: transform 0.2s ease-in;
}

.controls__hide-icon:hover {
    transform: scale(1.2);
}

@keyframes scaleIn {
    from {
        transform: scale(0.8);
    }
    to {
        transform: scale(1);
    }
}

@keyframes blackBackground {
    from {
        background: rgba(0, 0, 0, 0);
    }
    to {
        background: rgba(0, 0, 0, 0.3);
    }
}

@keyframes slideIn {
    from {
        transform: translateX(100%);
    }
    to {
        transform: translateX(0);
    }
}

